.contenedor{
	/*background: linear-gradient(315deg, #193157 0%, #346360 100%);*/
	background-repeat: no-repeat;
	font-family: arial;
	position: absolute;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 45px;
    font-weight: bold;
    background: linear-gradient(150deg, #ff0202,  #3e3f47, #2d49ba);
    background-size: 400% 300%;
    animation: BgGradient 5s ease infinite;
}

* {
	box-sizing: border-box;
	padding: 0;
    margin: 0;
}
  
.form {
	width: 400px;
	height: 400px;
	background: #ffffff;
	margin: auto;
	margin-top: 180px;
	box-shadow: 7px 13px 37px #000;
	padding: 20px 30px;
	border-radius: 10px;
	color: rgb(0, 0, 0);
}
  
.form h5 {
	margin: 0;
	text-align: center;
	height: 40px;
	margin-bottom: 18px;
	font-size: 30px;
	font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
  
.controls {
	width: 100%;
	border: none;
	margin-bottom: 13px;
	padding: 11px 10px;
	background: rgba(230.56249290704727, 230.56249290704727, 230.56249290704727, 1);
	font-size: 16px;
	/*font-weight: bold;*/
	color: rgba(97.74999797344208, 97.74999797344208, 97.74999797344208, 1);
	border-radius: 10px;
}

.controls::placeholder {
	color: rgba(97.74999797344208, 97.74999797344208, 97.74999797344208, 1);
}
  
.buttons1 {
	width: 50%;
	height: 40px;
	background: #052733;
	border: none;
	border-radius: 10px;
	margin-top: 18px;
	color: white;
	margin-bottom: 10px;
}

.buttons1:hover {
	background-color: #25363c;
	cursor:pointer;
}
  
.form p{
	height: 40px;
	text-align: center;
	margin-top: 10px;
}
  
.form a {
	color: rgb(0, 0, 0);
	text-decoration: none;
	font-size: 14px;
}
  
.form a:hover {
	text-decoration: underline;
}

@keyframes BgGradient {
    0% {background-position: 0% 50%;}
    50% {background-position: 100% 50%;}
    100% {background-position: 0% 50%;}
}