body{
    margin: 0px;
    overflow-x: hidden;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 18px;
    padding: 0;
    margin: 0;
    margin-left: var(--width-collapsed);
    background-color: #242424;
}

.bodyExpanded{
    margin-left: var(--width);
}

.mainContainer{
    color:none;
}

#sidemenu{
    background-color: var(--bgcolor);
    color: white;
    position: fixed;
    height: 100%;
    left: 0;
    top: 0;
    transition: 0.2s;
}

#sidemenu a{
    color: white;
    text-decoration: none;
}

#sidemenu #header{
    box-sizing: border-box;
    overflow: hidden;
}

#sidemenu #header #title{
    box-sizing: border-box;
    overflow: hidden;
}

#sidemenu #header #title, #sidemenu #header #menuBtn{
    vertical-align: middle;
}

#sidemenu #header #menuBtn{
    display: inline-block;
}

#sidemenu #header #menuBtn{
    cursor: pointer;
    padding: 10px;
    width: var(--width-collapsed);
    box-sizing: border-box;
}

#sidemenu #header #menuBtn:hover{
    background-color: var(--hovercolor);
}

#sidemenu #header #menuBtn .btnHam{
    background-color: #fff;
    width: 100%;
    height: 2px;
    margin: 5px 0;
}

#sidemenu #menuItems{
    overflow: hidden;
}

#sidemenu #menuItems .item{
    width: var(--width);
}

#sidemenu #menuItems .item .icon{
    display: inline-block;
}

#sidemenu #menuItems .item .icon,
#sidemenu #menuItems .item .title{
    font-size: 18px;
    vertical-align: middle;
    overflow: hidden;
    display: inline-block;
}

#sidemenu #menuItems .item a{
    display: block;
}

#sidemenu #menuItems .item a:hover{
    background-color: var(--hovercolor);
}

#sidemenu #menuItems .item .icon{
    box-sizing: border-box;
    padding: var(--padding);
    width: var(--width-collapsed);
}

#sidemenu #menuItems .item .icon img{
    width: 100%;
}

#sidemenu #menuItems .item .title{
    padding: var(--padding) 0;
}

#sidemenu #menuItems .item .separator{
    height: 1px;
    border-bottom: solid 1px var(--hovercolor);
    margin: 15px 0;
}

.menuExpanded{
    width: var(--width);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.menuExpanded #header #title{
    display: inline-block;
    width: calc(100% - 50px);
    margin-right: -5px;
    padding: var(--padding);
}

.menuExpanded #header #title span{
    width: calc(var(--width) - var(--width-collapsed) - 5px);
    overflow: hidden;
}

.menuCollapsed{
    width: var(--width-collapsed);
}

.menuCollapsed #header #title{
    width: 0;
    height: 0;
    padding: 0;
    margin: 0;
}

.menuCollapsed .item {
    width: 100px;
}

