/*Iconos con movimiento*/
.item:hover .icon{
    animation: effect 0.4s infinite;
}

@keyframes effect {
    0% {
        transform: translateX(0px) rotate(0deg);
    }

    20% {
        transform: translateX(-4px) rotate(-4deg);
    }

    40% {
        transform: translateX(-2px) rotate(-2deg);
    }

    60% {
        transform: translateX(4px) rotate(4deg);
    }

    80% {
        transform: translateX(2px) rotate(2deg);
    }

    100% {
        transform: translateX(0px) rotate(0deg);
    }
}

canvas {
    display: block;
    position: absolute;
    left: 55%;
    top: 50%;
    transform: translate(-50%, -50%);
    /* cursor: none; */
    width: 100%;
    height: 100%;
}