.box {
  width: 800px;  
  height: 300px;
  /*margin: 10% auto;*/
  position: relative;
  margin-left: 120px;
  margin-right: 0;
  margin-bottom: 100px;
}

#card-container {
  position: relative;
  margin: 30px;
  width: 250px;
  height: 250px;
  z-index: 1;
  float: left;
  perspective: 1000px;
}

.Imag {
  width: 250px;
  height: 250px;
  background-color: #ffffff;
}

#card, #card2{/*, #card3 {*/
  width: 100%;
  height: 100%;
  margin: 10%;
  transform-style: preserve-3d;
  transition: all .8s linear;
  box-shadow: 5px 5px 15px rgba(0,0,0, .3);
}

#card {
  &:hover {
    transform: rotateY(180deg);
    box-shadow: -5px 5px 15px rgba(0,0,0, .3);
  }
}

#card2 {
  &:hover {
    transform: rotateY(-180deg);
    box-shadow: -5px 5px 15px rgba(0,0,0, .3);
  }
}

